export default {
  data() {
    return {
			// 控制有消息和无消息内容的切换
			isBox: true,
			user_id: ''
		};
	},
	onLoad() {
		this.user_id = uni.getStorageSync("user_id");
		// 推荐明细接口
		this.getRecom()
	},
  methods: {
		// 推荐明细接口
		getRecom() {
			this.$http.get('/wallet/getRecom', { params: {user_id: this.user_id} }).then(res => {
				console.log(res)
			})
		},
		// 刷新
		refresh() {
			this.isBox = true	
		}
	}
};
